const AdminSidebar = () => {
  const router = useRouter();
  // console.log(router);
  
  const [showModal, setShowModal] = useState<boolean>(false);
  const [phoneActive, setPhoneActive] = useState<boolean>(false);

  useEffect(() => {
    // Define the resize handler
    const resizeHandler = () => {
      setPhoneActive(window.innerWidth < 1100);
      if (window.innerWidth > 1100)
        setShowModal(true);
    };

    // Set the initial state on the client side
    resizeHandler();

    // Add the event listener
    window.addEventListener("resize", resizeHandler);

    // Clean up the event listener on component unmount
    return () => {
      window.removeEventListener("resize", resizeHandler);
    };
  }, []);

  return (
    <>
      {phoneActive && <button className="text-xl" id="hamburger" onClick={() => setShowModal(true)}>
        <HiMenuAlt4 />
      </button>}

      {showModal && <aside
        className='absolute transition-all duration-600 w-[90%] max-w-[360px] rounded-lg flex flex-col justify-center items-center gap-2 bg-green-200'
      >
        <Image className="m-4 rounded-full flex justify-center items-center" src={`/download.jpeg`} alt="User" width={50} height={50} />

        {Object.entries(sideBarData).map((data, index) => {
          // console.log(data)
          return (
            <DataDiv data={data} router={router} key={index} />
          )
        })}
        {/* <DivOne router={router} />
        <DivTwo router={router} />
        <DivThree router={router} /> */}

        {phoneActive && (
          <button
            className="bg-red-500 rounded-md my-3 px-8 py-1"
            id="close-sidebar"
            onClick={() => setShowModal(false)}
          >
            Close
          </button>
        )}
      </aside>
      }
    </>
  );
};

type dataProps = {
  data: [name: string, value: {
    url: string;
    text: string;
    Icon: IconType;
  }[]],
  router: any
}

const DataDiv = ({ data: [name, value], router }: dataProps) => (
  <div className="w-[85%]">
    <h5 className="heading opacity-50 my-3">{name}</h5>
    <ul className="flex flex-col justify-center items-center gap-2 w-full">
      {value.map((i, index) => (
        <Li data={i} router={router} />
      ))}
    </ul>
  </div>
);

